<template lang="html">
	<div class="" style="">
		<div class="inquiry_title">
			<strong>证件查询 / </strong><span>说明说明说明说明说明说明说明说明</span>
		</div>
		<!--<div class="hello-bigbox"></div>-->
		<div class='hello-bo'>
			<div class="hello-box1">
				<span>身份证号：</span>
				<el-input size="large" placeholder="15620919870627676X" v-model="input6" class='s1'></el-input>


				<span>姓名：</span>
				<el-input size="large" placeholder="请输入要查询的姓名" v-model="input6" class='s1'></el-input>
			</div>
			<div class="hello-box2">
				<el-button type="primary" icon="search" class='icon'>查询</el-button>
				<el-button type="primary" icon="delete" class='icon'>清除</el-button>
				<el-button type="primary" icon="upload2" class='icon'>打印</el-button>
			</div>
			<!--<br /><br /><br /><br />-->
			<div class="img clearflx">
				<img src="../../assets/mine.png" />
				<div>
					<p style="font-size: 14px;padding-left: 5px;"><span>发证机关：</span><span>姓名</span><span>车牌号</span><span>手机号</span></p>
					<p style="font-size: 14px;padding-left: 5px;"><span>出生年月：</span><span>姓名</span><span>车牌号</span><span>手机号</span></p>
				</div>
			</div>
			<hr />


			<div class="hello-box3">
				<div class="left" style="display: flex;">
					<h2 style="color: #3251bd;font-weight:bold;">历史记录：</h2>
					<p style="color: #d88479;">特别提醒：只提供半年内的验证记录</p>
				</div>
				<div class="right">


					<div class="block">
						<span class="demonstration" style='font-size:12px;'>起始日期</span>
						<el-date-picker v-model="value1" type="datetime" placeholder="选择开始日期">
						</el-date-picker>
					</div>


					<div class="block">
						<span class="demonstration"></span>
						<el-date-picker v-model="value2" type="datetime" placeholder="选择结束日期">
						</el-date-picker>
						<el-button type="primary" icon="search" class='icon' style='background: #384ea3;'>查询</el-button>
					</div>
				</div>
			</div>
		</div>
	</div>


</template>


<script>
	export default {
		data() {
			return {
				input6: '',
				value1:'',
				value2:'',
			}
		}	
	}
</script>



<style lang='less' scoped>
	* {
		padding: 0;
		margin: 0;
	}
	
	.inquiry_title {
		height: 90px;
		line-height: 90px;
		background: #FAFBFF;
	}
	
	.inquiry_title span {
		color: #8894AE;
	}
	
	.inquiry_title strong {
		font-size: 20px;
		color: #3851A5;
	}
	
	.clearflx:after {
		display: block;
		clear: both;
		content: '';
	}
	
	.hello {
		width: 1400px;
		height: 670px;
		background: #fafbff;
	}
	.hello-bo{
		border:1px solid #e7e8ed;	
		height: 670px;
		padding: 20px;
		.hello .hello-box {
			width: 100%;
			
		}
		.hello .hello-box h2 {
			float: left;
			padding: 36px 0;
			color: #384ea3;
		}
		
		.hello .hello-box span {
			float: left;
			padding: 36px 0;
			color: #384ea3;
		}
		
		.hello-box1 {
			
			width: 100%;
			background: #FFF;
			float: left;
			padding: 20px 0;
		}
		
		.hello-box2 {
			float: right;
			width: 40%;
			margin-top: 20px;
		}
		
		.icon {
			background: #3052ac;
		}
		
		.s1 {
			width: 200px;
		}
		
		.img {
			width: 55%;
			padding: 20px 0;
			display: flex;
			font-size: 20px;
			color: #4f59a0;
		}
		
		.icon {
			width: 100px;
			height: 40px;
		}
		
		.left {
			
			height: 40px;
			line-height: 40px;
		}
		
		.right {
			display: flex;
			justify-content: space-between;
			width: 61%;
			height: 40px;
			line-height: 40px;
		}
		
		.hello-box3 {
			margin-top: 32px;
			display: flex;
			justify-content: space-between;
			width: 100%;
		}
		
		.hello-box1 span:nth-of-type(1) {
			font-size: 16px;
			color: #3052ac;
		}
		
		.hello-box1 span:nth-of-type(2) {
			font-size: 16px;
			margin-left: 20px;
				color: #3052ac;
		}
	}
	
	.demonstration{
		font-size: 16px;
		color: #3052ac;
	}
</style>


